<template>
  <el-card class="box-card box-card-div" v-loading="!this.categoryList.length">
    <div slot="header" class="clearfix">
      <span>文章分类</span>
      <el-button
        class="title_button"
        style="float: right; padding: 3px 0"
        type="text"
        @click="getArticlePage"
        >显示全部</el-button
      >
    </div>
    <div id="category_icon_div">
      <div
        v-for="category in categoryList"
        :key="category.id"
        class="text item"
      >
        <div>
          <i
            @click="categoryByName(category.id)"
            id="category_icon"
            class="el-icon-arrow-right"
          >
            <span> {{ category.categoryName }}</span></i
          >
        </div>
      </div>
    </div>
  </el-card>
</template>

<script>
export default {
  name: "CategoryPage",
  data() {
    return {
      // loading: true,
      pageInfo: {
        pageNum: 1,
        pageSize: 3,
        categoryId: null,
      },
      categoryList: [],
    };
  },
  methods: {
    async categoryByName(id) {
      this.pageInfo.categoryId = id;
      const res = await this.$api.article.getArticleListByCategory(
        this.pageInfo
      );
      if (res.data.code === 200) {
        this.$store.dispatch("asyncUpdateArticleList", res.data.data);
        this.$store.dispatch("asyncUpdatedTotal", res.data.total);
        this.$store.dispatch("asyncUpdatedCategory",id);
        this.$message.success("小的给您选好该类别文章啦~");
        document.getElementById("show").scrollIntoView();
      } else {
        this.$message.error("小的已经尽力了！请稍后再试~");
      }
    },
    async getCategoryList() {
      const res = await this.$api.category.getCategory();
      if (res.data.code === 200) {
        this.categoryList = res.data.data;
        
      } else {
        this.$message.error("获取分类信息失败！请稍后重试~");
      }
    },
    async getArticlePage() {
      const res = await this.$api.article.getArticlePage(this.pageInfo);
      if (res.data.code === 200) {
        window.sessionStorage.setItem(
          "articleList",
          JSON.stringify(res.data.data)
        );
        this.$store.dispatch("asyncUpdateArticleList", res.data.data);
        this.$store.dispatch("asyncUpdatedTotal", res.data.total);
        this.$store.dispatch("asyncUpdatedCategory",null);
        this.$message.success("全部文章新鲜出炉咯~");
        document.getElementById("show").scrollIntoView();
      } else {
        this.$message.error("获取文章列表失败！");
      }
    },
  },
  mounted() {
    this.getCategoryList();
  },
};
</script>

<style scoped>
.box-card {
  width: 100%;
  height: 100%;
  margin-top: 20px;
  border-radius: 10px;
}
.title_button {
  color: #c353e8;
  /* background-color:#E9B5FA; */
  /* border: solid 1px #C353E8; */
}
#category_icon {
  display: inline;
  float: left;
  margin-right: 25px;
  margin-left: 20px;
  margin-bottom: 25px;
  width: 100px;
  font-size: 13px;
}
#category_icon_div {
  margin-bottom: 25px;
}
</style>