<template>
  <div id="show">
    <div class="block" v-if="!isphone">
      <el-carousel>
        <el-carousel-item v-for="item in carouselImgs" :key="item">
          <img class="carouselImg_size" v-bind:src="item" alt="" />
        </el-carousel-item>
      </el-carousel>
    </div>

    <div class="block_phone" v-if="isphone">
      <el-carousel height="180px">
        <el-carousel-item v-for="item in carouselImgs" :key="item">
          <img class="carouselImg_size_phone" v-bind:src="item" alt="" />
        </el-carousel-item>
      </el-carousel>
    </div>
  </div>
</template>

<script>
export default {
  name: "CarouselPage",
  data() {
    return {
      isphone:false,
      carouselImgs: [
        "http://kodo.yelingfa.top/lyblog/static/carousel1.jpg",
        "http://kodo.yelingfa.top/lyblog/static/carousel5.png",
        "http://kodo.yelingfa.top/lyblog/static/carousel3.jpg",
        "http://kodo.yelingfa.top/lyblog/static/carousel4.jpg",
      ],
    };
  },
  methods:{
    computedWidth() {
      var width = window.document.body.clientWidth;
      if (width < 500) {
        this.isphone = true
      } else {
        this.isphone = false
      }
    },
  },
  mounted(){
    this.computedWidth()
  }
};
</script>

<style scoped>
.block {
  width: 100%;
  height: 300px;
  margin-top: 70px;
}
.block_phone {
  width: 100%;
  height: 180px;
  margin-top: 8px;
}
.carouselImg_size {
  width: 100%;
  height: 300px;
  border-radius: 10px;
}
.carouselImg_size_phone {
  width: 100%;
  height: 180px;
  border-radius: 10px;
}
</style>
<style>
.el-carousel__item is-active is-animating{
  height: 180px !important;
}
</style>