<template>
  <div v-loading="!$store.getters.getArticleList">
    <div v-if="!isphone">
      <el-card
        class="box-card"
        v-for="article in $store.getters.getArticleList"
        :key="article.id"
      >
        <div @click="pushArticleDetail(article.id)">
          <el-row type="flex" class="row-bg" justify="space-between">
            <el-col :span="8">
              <div class="article_img_div">
                <img class="article_img" :src="article.articleimgurl" alt="" />
              </div>
            </el-col>
            <el-col :span="16">
              <div class="article_div">
                <div class="article_title">
                  <h3 style="margin-top: 10px; margin-bottom: 9px">
                    {{ article.articletitle }}&nbsp;&nbsp;
                    <span
                      v-if="article.istop"
                      style="color: #c353e5; magin-left: 25px"
                      >{{ article.istop > 0 ? "置顶" : "置顶" }}</span
                    >
                  </h3>
                  <hr />
                </div>

                <div class="article_explain_div">
                  <span class="article_explain">{{
                    article.articleaccount
                  }}</span>
                </div>
                <div class="article_create">
                  <div class="article_icon_div">
                    <el-row :gutter="0">
                      <el-col :xs="17" :sm="17" :md="18" :lg="18" :xl="18"
                        ><i class="el-icon-user article_icon">
                          <span> {{ article.createbyName }} </span></i
                        >

                        <i class="el-icon-view article_icon">
                          <span> {{ bigNumberTransform(article.review) }} </span></i
                        ><i class="el-icon-chat-dot-round article_icon">
                          <span> {{ bigNumberTransform(article.commentSum) }} </span></i
                        >
                        <i class="el-icon-time article_icon">
                          <span> {{ timePase(article.createtime) }} </span></i
                        >
                      </el-col>
                      <el-col :xs="7" :sm="7" :md="6" :lg="6" :xl="6"
                        ><el-tag class="category-div" type="success"><span> {{
                          article.category
                        }} </span></el-tag></el-col
                      >
                    </el-row>
                  </div>
                </div>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-card>
    </div>
    <!-- 手机适配版本 -->
    <div v-if="isphone">
      <el-card
        class="box-card-phone"
        v-for="article in $store.getters.getArticleList"
        :key="article.id"
      >
        <div @click="pushArticleDetail(article.id)">
          <el-row type="flex" class="row-bg" justify="space-between">
            <el-col :span="8">
              <div class="article_img_div_phone">
                <img
                  class="article_img_phone"
                  :src="article.articleimgurl"
                  alt=""
                />
              </div>
            </el-col>
            <el-col :span="16">
              <div class="article_div_phone">
                <div class="article_title_phone">
                  <h3 style="margin-top: 10px; margin-bottom: 8px">
                    {{ article.articletitle }}&nbsp;&nbsp;
                    <span
                      v-if="article.istop"
                      style="color: #c353e5; magin-left: 25px"
                      >{{ article.istop > 0 ? "置顶" : "置顶" }}</span
                    >
                  </h3>
                  <hr />
                </div>

                <div class="article_explain_div_phone">
                  <span class="article_explain_phone">{{
                    article.articleaccount
                  }}</span>
                </div>
                <div class="article_create_phone">
                  <div class="article_icon_div_phone">
                    <el-row :gutter="0">
                      <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24"
                        ><i class="el-icon-user article_icon_phone">
                          <span> {{ article.createbyName }} </span></i
                        >

                        <i class="el-icon-time article_icon_phone">
                          <span> {{ timePase(article.createtime) }} </span></i
                        >
                        <el-tag class="category-div_phone" type="success"><span> {{
                          article.category
                        }} </span></el-tag></el-col
                      >
                    </el-row>
                  </div>
                </div>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-card>
    </div>
    <el-pagination
      class="page_class"
      background
      :page-size="3"
      layout="prev, pager, next"
      :total="$store.getters.getTotal"
      v-if="!isphone"
      @current-change="changePageNum"
    >
    </el-pagination>
    <el-pagination
      class="page_class_phone"
      background
      :page-size="3"
      layout="prev, pager, next"
      :total="$store.getters.getTotal"
      v-if="isphone"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: "ArticlePage",
  data() {
    return {
      isphone: false,
      loading: true,
      pageInfo: {
        pageNum: 1,
        pageSize: 3,
        categoryId: null,
      },
      total: "",
      articleList: [
        // {
        //   img: "/images/article1.jpg",
        //   title: "Hello,World!",
        //   explain:
        //     "这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！",
        //   context: "",
        //   author: "小叶同学",
        //   view: "200",
        //   commentNum: "10",
        //   createTime: "2023-01-01",
        // },
        // {
        //   img: "/images/article2.jpg",
        //   title: "你好，世界！",
        //   explain:
        //     "这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！",
        //   context: "",
        //   author: "小叶同学",
        //   view: "202220",
        //   commentNum: "1000",
        //   createTime: "2023-12-30",
        // },
        // {
        //   img: "/images/article3.jpg",
        //   title: "欢迎您的到来！",
        //   explain:
        //     "这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！这是一段描述！",
        //   context: "",
        //   author: "小叶同学",
        //   view: "1",
        //   commentNum: "1000000",
        //   createTime: "2023-01-01",
        // },
      ],
    };
  },
  methods: {
    async changePageNum(num) {
      this.pageInfo.pageNum = num;
      this.pageInfo.categoryId = this.$store.getters.getCategory;
      const res = await this.$api.article.getArticlePage(this.pageInfo);
      if (res.data.code === 200) {
        this.articleList = res.data.data;
        window.sessionStorage.setItem(
          "articleList",
          JSON.stringify(this.articleList)
        );
        this.$store.dispatch("asyncUpdateArticleList", res.data.data);
        this.$store.dispatch("asyncUpdatedTotal", res.data.total);
      } else {
        this.$message.error("获取文章列表失败！");
      }
    },
    async pushArticleDetail(id) {
      // const res = await this.$api.article.getArticleDetailById(id);
      // if (res.data.code === 200) {
      //   window.sessionStorage.setItem(
      //     "articleDetail",
      //     JSON.stringify(res.data.data)
      //   );
      //   this.$store.dispatch("asyncUpdatedArticleDetail", res.data.data);
      this.$router.push("/articleDetail/" + id);
      // } else {
      //   this.$message.error("获取文章详情失败！请稍后再试");
      //   this.$router.push("/error");
      // }
    },
    async getArticlePage() {
      const res = await this.$api.article.getArticlePage(this.pageInfo);
      if (res.data.code === 200) {
        this.articleList = res.data.data;
        window.sessionStorage.setItem(
          "articleList",
          JSON.stringify(this.articleList)
        );
        this.$store.dispatch("asyncUpdateArticleList", res.data.data);
        this.$store.dispatch("asyncUpdatedTotal", res.data.total);
      } else {
        this.$message.error("获取文章列表失败！");
      }
    },
    computedWidth() {
      var width = window.document.body.clientWidth;
      if (width < 500) {
        this.isphone = true;
      } else {
        this.isphone = false;
      }
    },
  },
  mounted() {
    this.getArticlePage();
    this.computedWidth();
  },
  computed: {
    bigNumberTransform() {
      return function (value) {
        const newValue = ["", "", ""];
        let fr = 1000;
        let num = 3;
        let text1 = "";
        let fm = 1;
        while (value / fr >= 1) {
          fr *= 10;
          num += 1;
          // console.log('数字', value / fr, 'num:', num)
        }
        if (num <= 4) {
          // 千
          newValue[0] = parseInt(value / 1000) + "";
          newValue[1] = "千";
        } else if (num <= 8) {
          // 万
          text1 = parseInt(num - 4) / 3 > 1 ? "千万" : "万";
          // tslint:disable-next-line:no-shadowed-variable
          fm = text1 === "万" ? 10000 : 10000000;
          if (value % fm === 0) {
            newValue[0] = parseInt(value / fm) + "";
          } else {
            newValue[0] = parseFloat(value / fm).toFixed(2) + "";
          }
          newValue[1] = text1;
        } else if (num <= 16) {
          // 亿
          text1 = (num - 8) / 3 > 1 ? "千亿" : "亿";
          text1 = (num - 8) / 4 > 1 ? "万亿" : text1;
          text1 = (num - 8) / 7 > 1 ? "千万亿" : text1;
          // tslint:disable-next-line:no-shadowed-variable
          fm = 1;
          if (text1 === "亿") {
            fm = 100000000;
          } else if (text1 === "千亿") {
            fm = 100000000000;
          } else if (text1 === "万亿") {
            fm = 1000000000000;
          } else if (text1 === "千万亿") {
            fm = 1000000000000000;
          }
          if (value % fm === 0) {
            newValue[0] = parseInt(value / fm) + "";
          } else {
            newValue[0] = parseFloat(value / fm).toFixed(2) + "";
          }
          newValue[1] = text1;
        }
        if (value < 1000) {
          newValue[0] = value + "";
          newValue[1] = "";
        }
        return newValue.join("");
      };
    },

    timePase() {
      return function (time) {
        var date = new Date(time); // 初始化日期
        var year = date.getFullYear();
        var month = date.getMonth() + 1; // 获取月份
        var day = date.getDate(); // 获取具体日
        var hour = date.getHours();
        var minute = date.getMinutes();
        return (
          year +
          "-" +
          month +
          "-" +
          day +
          " " +
          (hour < 10 ? "0" + hour : hour) +
          ":" +
          (minute < 10 ? "0" + minute : minute)
        );
      };
    },
  },
};
</script>

<style scoped>
