<script>
export default {
  name: "UserPage",
  data() {
    return {
      show: 90,
      comment: 180,
    };
  },
  methods: {
    github() {
      window.location.href = "https://github.com/Yelf-0h";
    },
    // getShowData(){
    //   const articleList = JSON.parse(window.sessionStorage.getItem("articleList"))
    //   var showSum = 0;
    //   var commentSum = 0;
    //   for (let index = 0; index < articleList.length; index++) {
    //     const element = articleList[index];
    //     showSum = showSum + element.review;
    //     commentSum = commentSum + element.commentSum;
    //   }
    //   this.show = showSum;
    //   this.comment = commentSum;
    // }
  },
  mounted(){
    // this.getShowData()
  },
  computed: {
    bigNumberTransform() {
      return function (value) {
        const newValue = ["", "", ""];
        let fr = 1000;
        let num = 3;
        let text1 = "";
        let fm = 1;
        while (value / fr >= 1) {
          fr *= 10;
          num += 1;
          // console.log('数字', value / fr, 'num:', num)
        }
        if (num <= 4) {
          // 千
          newValue[0] = parseInt(value / 1000) + "";
          newValue[1] = "千";
        } else if (num <= 8) {
          // 万
          text1 = parseInt(num - 4) / 3 > 1 ? "千万" : "万";
          // tslint:disable-next-line:no-shadowed-variable
          fm = text1 === "万" ? 10000 : 10000000;
          if (value % fm === 0) {
            newValue[0] = parseInt(value / fm) + "";
          } else {
            newValue[0] = parseFloat(value / fm).toFixed(2) + "";
          }
          newValue[1] = text1;
        } else if (num <= 16) {
          // 亿
          text1 = (num - 8) / 3 > 1 ? "千亿" : "亿";
          text1 = (num - 8) / 4 > 1 ? "万亿" : text1;
          text1 = (num - 8) / 7 > 1 ? "千万亿" : text1;
          // tslint:disable-next-line:no-shadowed-variable
          fm = 1;
          if (text1 === "亿") {
            fm = 100000000;
          } else if (text1 === "千亿") {
            fm = 100000000000;
          } else if (text1 === "万亿") {
            fm = 1000000000000;
          } else if (text1 === "千万亿") {
            fm = 1000000000000000;
          }
          if (value % fm === 0) {
            newValue[0] = parseInt(value / fm) + "";
          } else {
            newValue[0] = parseFloat(value / fm).toFixed(2) + "";
          }
          newValue[1] = text1;
        }
        if (value < 1000) {
          newValue[0] = value + "";
          newValue[1] = "";
        }
        return newValue.join("");
      };
    },
  },
};
</script>

