<template>
  <!-- <el-row type="flex" class="row-bg" justify="space-between">
    <el-col :span="8"> -->
  <el-card id="box-card" class="box-card" v-loading="!this.linkUserList.length">
    <div slot="header" class="clearfix">
      <span>好友博客</span>
    </div>
    <div>
      <div
        id="link_icon"
        v-for="linkUser in linkUserList"
        :key="linkUser.id"
        class="text item"
      >
        <i class="el-icon-s-custom">
          <span> {{ linkUser.linkName }} </span>
        </i>
        <a class="linkUrl" style="margin-left: 12px" :href="linkUser.linkUrl"
          >点击前往
        </a>
      </div>
    </div>
  </el-card>
  <!-- </el-col>
  </el-row> -->
</template>

<script>
export default {
  name: "LinkPage",
  data() {
    return {
      linkUserList: []
    };
  },
  methods:{
    async getLinkList(){
      const res = await this.$api.link.linkList()
      if(res.data.code === 200){
        this.linkUserList = res.data.data
        console.log(this.linkUserList);
      }else{
        this.$message.error("获取友链失败！")
      }
    }
  },
  mounted() {
    // if(this.linkUserList.length){
    //   this.loading.
    // }
    this.getLinkList()
    
  },
};
</script>

<style scoped>
.box-card {
  width: 100%;
  margin-top: 20px;
  height: auto;
  border-radius: 10px;
  margin-right: 20px;
}
#link_icon {
  margin-top: 15px;
  margin-left: 20px;
  margin-bottom: 25px;
}
.linkUrl{
  float: right;
  margin-right: 20px;
  z-index: 10;
  clear: both;
}
</style>